<template>
	<gui-page :customFooter="true" :footerSets="{height:150, zIndex:100, bg:'none'}" style="background-color: #f6f7fa;">
		<view slot="gBody" style="background-color: white;padding: 2%;">
			<view class="gui-margin-top gui-padding-x gui-flex gui-row gui-wrap gui-space-between">
				<view class="gui-product" hover-class="gui-tap" v-for="(item, index) in products" :key="index" v-if="item.love"
					style="margin-bottom: 10rpx;background-color: white;box-shadow:0px 0px 3px 1px rgba(0, 0, 0, 0.08);padding: 1%;margin-right: 1rpx;"
					@tap="gotoInfo(index, item)">
					<view>
						<gui-image :src="'http://192.168.5.2/dev-api'+item.img" :width="320" :height="320"></gui-image>
					</view>
					<view class="gui-product-lines">
						<text class="gui-product-name gui-primary-text">{{item.title}}</text>
					</view>
					<view
						class="gui-product-lines gui-flex gui-row gui-nowrap gui-align-items-center gui-space-between">
						<view class="gui-product-lines gui-flex gui-row gui-nowrap gui-align-items-center " >
							<text class="gui-color-red gui-block gui-text-small">积分</text>
							<text class="gui-h6 gui-block gui-color-red">{{item.price}}</text>
							<!-- <view style="margin-left:12rpx;">
								<text
									class="gui-text-small gui-block gui-color-gray gui-line-through">{{item.priceMarket}}</text>
							</view> -->
						</view>
						<!-- <text class="gui-text-small gui-block gui-color-gray">{{item.selledNum}}人购买</text> -->
					</view>
					<view style="height:30rpx;"></view>
				</view>
			</view>
		</view>
	</gui-page>
</template>
<script>
	var graceJS = require('@/GraceUI5/js/grace.js');
	var request = require('@/GraceUI5/js/request/request.js');
	export default {
		data() {
			return {
				products: []
			}
		},
		onLoad: function() {
			// 加载产品数据
			// uni.request({
			// 	url: 'https://www.graceui.com/api/products/lists',
			// 	success: (res) => {
			// 		// console.log(res);
			// 		this.products = res.data.data;
			// 	}
			// })
			this.getshopList()
		},
		methods: {
			getshopList() {
				request.post("bohe/getShopList", {
					userId:graceJS.getStorage("id")
				}, (res) => {
					console.log(res)
					this.products = res.data.data
				})
			},
			gotoInfo: function(idx, item) {
				console.log('商品索引 : ' + idx);
				uni.navigateTo({
					url:"./shop?id=" + item.id
				})
			}
		}
	}
</script>
<style scoped>
	.gui-product {
		width: 330rpx;
		overflow: hidden;
	}

	.gui-product-lines {
		margin-top: 10rpx;
	}

	.gui-product-name {
		font-size: 28rpx;
		line-height: 32rpx;
	}
</style>WW